<template>
  <div id="axios-get">
    <h1>{{title}}</h1>
    <br>
    <hr>
    <br>
    <ul>
      <li v-for="(item,index) in datas.banner" :key="index">
        <h2>{{item.title}}</h2>
        <p>{{item.content}}</p>
        <img :src="item.img" alt srcset style="width:500px">
        <p></p>
        <br>
        <hr>
        <br>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "axios-get",
  data() {
    return {
      title: "【axios-get请求的数据】",
      datas: []
    };
  },
  created() {
    this.$axios
      .get("/sxtstu/blueberrypai/getIndexBanner.php")
      .then(result => {
        console.log(result.data);
        this.datas = result.data;
      })
      .catch(err => {
        console.log(err);
      });
  }
};
</script>
<style scoped>
</style>




